@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
body{font-family:"Nunito",sans-serif;background-image:linear-gradient(90deg, #a1c4fd 10%, #c2e9fb 90%);padding-top:81px}.image-carousel{object-fit:cover;width:100%;height:800px}#brand-text{font-family:"Amatic SC",cursive;font-size:2.3rem;font-weight:700;background:#ffb76b;background:linear-gradient(to right, #ffb76b 0%, #ffa73d 30%, #ff7c00 60%, #ff7f04 100%);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.is-required{color:crimson}.swal-list{list-style:none}.styled-button{margin-left:154px}.wine-color{color:#800020}.nice-red-color{color:#ff4d4f;font-weight:500}#my-container{max-width:1700px;background-color:#f0f0f0}.light-background{background-color:#fafafa}a{text-decoration:none;display:inline-block;position:relative}.effect-underline:after{content:"";position:absolute;left:0;display:inline-block;height:1em;width:100%;border-bottom:1px solid;margin-top:10px;opacity:0;transition:opacity .35s,transform .35s;transform:scale(0, 1)}.effect-underline:hover:after{opacity:1;transform:scale(1)}
